::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: $white-color;
}

::-webkit-scrollbar {
  width: 6px;
  background-color: $white-color;
}

::-webkit-scrollbar-thumb {
  background-color: $accent-color;
}
